import { Component, ChangeDetectorRef } from '@angular/core';
import { AuthProvidersService } from './providers/auth-providers.service';
import { Router, NavigationEnd } from '@angular/router';
import { UIkit } from 'uikit';
import { MediaMatcher } from '@angular/cdk/layout';
declare var UIkit: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'app';
  mobileQuery: MediaQueryList;
  private _mobileQueryListener: () => void;

  currentUser: any;
  fillerNav = Array(50).fill(0).map((_, i) => `Nav Item ${i + 1}`);
  fillerContent = Array(50).fill(0).map(() =>
      `Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut
       labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco
       laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in
       voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat
       cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.`);

  constructor(
    public authData: AuthProvidersService,
    private router: Router,
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher
  ) {
    this.mobileQuery = media.matchMedia('(max-width: 767px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);


    authData.currentUser().subscribe((data) => {
      this.currentUser = data;
    });

    this.router.events.subscribe(event => {
        if (event instanceof NavigationEnd) {
            this.checkIfAuthPages();
        }
    });
  }

  logout() {
    this.authData.logoutUser().then(() => {
      this.router.navigate(['/login']);
    });
  }

  checkIfAuthPages() {
    return window.location.href.indexOf("login") > -1 || window.location.href.indexOf("register") > -1
  }

  ngOnDestroy(): void {
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }
}



