import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { CompetitionProvidersService } from '../../../providers/competitions/competition-providers.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-add-competitions',
  templateUrl: './add-competitions.component.html',
  styleUrls: ['./add-competitions.component.scss']
})
export class AddCompetitionsComponent implements OnInit {

  competitionTypes: any;
  form: FormGroup;
  competitionData: any;
  constructor(
    public dialogRef: MatDialogRef<AddCompetitionsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public compService: CompetitionProvidersService,
    private formBuilder: FormBuilder) {

      compService.getCompetitionTypes().valueChanges().subscribe((data) => {
        this.competitionTypes = data;
      })

      this.form = this.formBuilder.group({
        shortName: [this.data ? this.data.shortName : null, Validators.required],
        description: [this.data ? this.data.description : null, Validators.required],
        type: [this.data ? this.data.type : null, Validators.required],
        numberOfClubs: [this.data ? this.data.numberOfClubs : null]
      })
    }

  ngOnInit() {}

  onCancel(): void {
    this.dialogRef.close();
  }


  addCompetition() {
    this.competitionData = this.form.value;
    this.competitionData.id = this.data ? this.data.id : null;
    if(this.data.id) {
      this.compService.updateCompetition(this.competitionData);
    }
    else {
      this.compService.addCompetition(this.competitionData);
    }
    this.dialogRef.close();
  }
}



